*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.data{
    width: 100vw;
    height: 85vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
    position: relative;

}
iframe {
    aspect-ratio: 2/1;
    width: 100%;
    border: 0;
}
.data{
    background-size:cover; /* Postavljanje veliƒçine slike */
}


.data::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(8, 6, 6, 0.75) ;

}
